<template>
    <div class="container">
        <h2>表格</h2>

        <PExample :code="code">
            <HTable :tableData="tableData" :tableLabel="tableLabel" :paginationData="paginationData" :tableAttrs="tableAttrs" :summaryMethod="summaryMethod" :spanMethod="spanMethod" @onCurrentChange="onCurrentChange" @onSizeChange="onSizeChange"></HTable>
        </PExample>

        <h3>属性</h3>
        <PAttrTable :data="attrs" />

        <h3>事件</h3>
        <PEventTable :data="events" />
    </div>
</template>

<script>
