import PAttrTable from '@/components/PAttrTable.vue'
import PEventTable from '@/components/PEventTable.vue'
import PExample from '@/components/PExample.vue'
import { findOrderList } from './api'
export default {
    name: 'H-Table',
    components: {
        HTable, PAttrTable, PEventTable, PExample
    },
    data () {
        return {
            queryParams: {
                statusSet: '2,3,4,5',
                pageNumber: 1,
                pageSize: 10
            },
            tableLabel: [
                { label: '订单号', prop: 'orderNo' },
                { label: '商品名称', prop: 'productName' },
                { label: '订单总额（元）', prop: 'totalAmount', format: 'money' },
                { label: '下单时间', prop: 'orderTime', format: 'YYYY-MM-DD HH:mm:ss' },
                { label: '最后操作用户', prop: 'lastModifyUser' }
            ],
            tableData: [],
            paginationData: {},
            tableAttrs: {
                stripe: false,
                actionWidth: 140
            },
            attrs: [
                {
                    param: 'tableData',
                    desc: '显示的数据',
                    type: 'array',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'tableLabel',
                    desc: '表头的数据',
                    type: 'array',
                    optional: '-',
                    default: '-',
                    children: [
                        {
                            param: 'prop',
                            desc: '对应列内容的字段名，也可以使用 property 属性',
                            type: 'string',
                            optional: '-',
                            default: '-'
                        },
                        {
                            param: 'label',
                            desc: '显示的标题',
                            type: 'string',
                            optional: '-',
                            default: '-'
                        },
                        {
                            param: 'width',
                            desc: '对应列的宽度',
                            type: 'string',
                            optional: '-',
                            default: '-'
                        },
                        {
                            param: 'fixed',
                            desc: '列是否固定在左侧或者右侧，true 表示固定在左侧',
                            type: 'string, boolean',
                            optional: 'true, left, right',
                            default: '-'
                        },
                        {
                            param: 'align',
                            desc: '对齐方式',
                            type: 'String',
                            optional: 'left/center/right',
                            default: 'center'
                        },
                        {
                            param: 'format',
                            desc: '格式化数据',
                            type: 'String',
                            optional: 'money/YYYY-MM-DD HH:mm',
                            default: 'money/YYYY-MM-DD HH:mm'
                        },
                        {
                            param: 'toFixed',
                            desc: '保留小数后几位',
                            type: 'number',
                            optional: '-',
                            default: '2'
                        },
                        {
                            param: 'showOverflowTooltip',
                            desc: '是否带有纵向边框',
                            type: 'boolean',
                            optional: '-',
                            default: 'true'
                        }
                    ]
                },
                {
                    param: 'paginationData',
                    desc: '分页的数据',
                    type: 'object',
                    optional: '-',
                    default: '-',
                    children: [
                        {
                            param: 'pageNumber',
                            desc: '当前页',
                            type: 'number',
                            optional: '-',
                            default: '1'
                        },
                        {
                            param: 'pageSize',
                            desc: '每页展示条数',
                            type: 'number',
                            optional: '-',
                            default: '10'
                        },
                        {
                            param: 'total',
                            desc: '总条数',
                            type: 'number',
                            optional: '-',
                            default: '-'
                        }
                    ]
                },
                {
                    param: 'tableAttrs',
                    desc: 'table属性',
                    type: 'object',
                    optional: '-',
                    default: '-',
                    children: [
                        {
                            param: 'border',
                            desc: '是否带有纵向边框',
                            type: 'boolean',
                            optional: '-',
                            default: 'true'
                        },
                        {
                            param: 'stripe',
                            desc: '是否为斑马纹 table',
                            type: 'boolean',
                            optional: '-',
                            default: 'true'
                        },
                        {
                            param: 'isMultiple',
                            desc: '是否显示多选框',
                            type: 'boolean',
                            optional: '-',
                            default: 'false'
                        },
                        {
                            param: 'isAction',
                            desc: '是否显示操作列',
                            type: 'boolean',
                            optional: '-',
                            default: 'false'
                        },
                        {
                            param: 'isPagination',
                            desc: '是否显示分页',
                            type: 'boolean',
                            optional: '-',
                            default: 'true'
                        },
                        {
                            param: 'isIndex',
                            desc: '是否显示序号列',
                            type: 'boolean',
                            optional: '-',
                            default: 'false'
                        },
                        {
                            param: 'showSummary',
                            desc: '是否在表尾显示合计行',
                            type: 'boolean',
                            optional: '-',
                            default: 'false'
                        },
                        {
                            param: 'actionWidth',
                            desc: '操作列的宽度',
                            type: 'number',
                            optional: '-',
                            default: '100'
                        },
                        {
                            param: 'maxHeight',
                            desc: 'table 的最大高度',
                            type: 'number',
                            optional: '-',
                            default: '-'
                        }
                    ]
                },
                {
                    param: 'paginationAttrs',
                    desc: '分页属性',
                    type: 'object',
                    optional: '-',
                    default: '-',
                    children: [
                        {
                            param: 'layout',
                            desc: '组件布局，子组件名用逗号分隔',
                            type: 'String',
                            optional: 'sizes, prev, pager, next, jumper, ->, total, slot',
                            default: `'prev, pager, next, jumper, ->, total'`
                        },
                        {
                            param: 'pageSizes',
                            desc: '每页显示个数选择器的选项设置',
                            type: 'number[]',
                            optional: '-',
                            default: '[10, 20, 30, 40, 50, 100]'
                        }
                    ]
                },
                {
                    param: 'summaryMethod',
                    desc: '自定义的合计计算方法',
                    type: 'Function({ columns, data })',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'spanMethod',
                    desc: '合并行或列的计算方法',
                    type: 'Function({ row, column, rowIndex, columnIndex })',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'selectable',
                    desc: '仅对 type=selection 的列有效，类型为 Function，Function 的返回值用来决定这一行的 CheckBox 是否可以勾选',
                    type: 'Function(row, index)',
                    optional: '-',
                    default: '-'
                }
            ],
            events: [
                {
                    name: 'multiSelection',
                    desc: '多选时触发的事件',
                    param: 'row'
                },
                {
                    name: 'onCurrentChange',
                    desc: 'currentPage 改变时会触发',
                    param: 'paginationData对象'
                },
                {
                    name: 'onSizeChange',
                    desc: 'pageSize 改变时会触发',
                    param: '每页条数'
                }
            ],
            code: `
<template>
    <Table></Table>
</template>

<script>
    import Table from '@/components/HTable.vue'

    export default {
        name: 'table',
        components: {
            Table
        }
    }
<\/script>`
        }
    },
    methods: {
        summaryMethod ({ columns, data }) {
            const sums = []
            columns.forEach((column, index) => {
                if (index == 0) {
                    sums[index] = '合计'
                }
                if (column.property == 'totalAmount') {
                    const values = data.map(item => Number(item[column.property]))
                    if (!values.every(value => isNaN(value))) {
                        sums[index] = values.reduce((prev, curr) => {
                            const value = Number(curr)
                            if (!isNaN(value)) {
                                return prev + curr
                            } else {
                                return prev
                            }
                        }, 0)
                        sums[index] = this.formatMoney(sums[index], 2) + ''
                    }
                }
            })
            return sums
        },
        spanMethod ({ row, column, rowIndex, columnIndex }) {
        },
        async getOrderList () {
            const { data } = await findOrderList(this.queryParams)
            this.tableData = data.records
            this.paginationData = {
                pageNumber: data.current,
                pageSize: data.size,
                total: data.total
            }
        },
        onCurrentChange (val) {
            this.queryParams.pageNumber = val.pageNumber
            this.getOrderList()
        },
        onSizeChange (val) {
            this.queryParams.pageSize = val
            this.getOrderList()
        },
        formatMoney (value, type) {
            if (value === '' || value === 0) return '0.00'
            value = Number(value)
            if (isNaN(value)) return '-'
            return value.toFixed(type).replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
        }
    },
    mounted () {
        this.getOrderList()
    }
}
</script>

