<template>
    <div class="page-table clearfix">
        <el-table :data="tableData" :border="tableAttrsInfo.border" :stripe="tableAttrsInfo.stripe" :max-height="tableAttrsInfo.maxHeight" :show-summary="tableAttrsInfo.showSummary" :summary-method="summaryMethod" :span-method="spanMethod" @selection-change="handleSelectionChange" :lazy="true">
            <el-table-column type="selection" :align="tableColAttrsInfo.align" :selectable="selectable" v-if="tableAttrsInfo.isMultiple"></el-table-column>
            <el-table-column type="index" :align="tableColAttrsInfo.align" label="序号" :index="indexMethod" v-if="tableAttrsInfo.isIndex"></el-table-column>
            <el-table-column v-for="item in tableLabel" :key="item.label" :label="item.label" :prop="item.prop" :min-width="item.width?item.width:''" :align="item.align?item.align:'center'" :fixed="item.fixed" :show-overflow-tooltip="item.showOverflowTooltip?item.showOverflowTooltip:true">
                <template slot-scope="scope">
                    <slot :name="item.prop" :data="scope" v-if="item.format == 'money'">
                        {{scope.row[item.prop] | formatMoney(item.toFixed?item.toFixed:2)}}</slot>
                    <slot :name="item.prop" :data="scope" v-else-if="!item.format">
                        {{formatter(scope.row[item.prop])}}</slot>
                    <slot :name="item.prop" :data="scope" v-else>
                        {{scope.row[item.prop] | formatDate(item.format)}}</slot>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" v-if="tableAttrsInfo.isAction" :min-width="tableAttrsInfo.actionWidth">
                <template slot-scope="scope">
                    <el-button class="greenBtn">新增{{scope.row.name}}</el-button>
                    <el-button class="orangeBtn">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination v-if="tableAttrsInfo.isPagination && paginationInfo.total" :total="paginationInfo.total" :current-page="paginationInfo.pageNumber" :page-size.sync="paginationInfo.pageSize" :layout="paginationAttrsInfo.pageLayout" :page-sizes="paginationAttrsInfo.pageSizes"
            @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination>
    </div>
</template>
<script>
export default {
    name: 'HTable',
    data () {
        return {
            paginationInfo: ''
        }
    },
    props: {
        tableLabel: {
            type: Array,
            default: () => {

            }
        },
        tableData: {
            type: Array,
            default: () => {

            }
        },
        tableAttrs: {
            type: Object,
            default: () => {

            }
        },
        tableColAttrs: {
            type: Object,
            default: () => {

            }
        },
        paginationAttrs: {
            type: Object,
            default: () => {

            }
        },
        summaryMethod: {
            type: Function,
            default: () => {

            }
        },
        spanMethod: {
            type: Function,
            default: () => {

            }
        },
        selectable: {
            type: Function,
            default: () => true
        },
        paginationData: {
            type: Object,
            default: () => {
                return {
                    total: 10,
                    pageNumber: 1,
                    pageSize: 10
                }
            }
        },
        isNull: {
            type: Boolean,
            default: false,
            desc: '当数据为空的时候是否空白显示，false的时候默认显示为-'
        }
    },
    computed: {
        tableAttrsInfo () {
            const tableAttrs = Object.assign({
                border: true,
                stripe: true,
                isMultiple: true,
                isAction: true,
                isPagination: true,
                isIndex: true,
                showSummary: true,
                actionWidth: 100,
                maxHeight: 10000000
            }, this.tableAttrs)
            return tableAttrs
        },
        tableColAttrsInfo () {
            const tableColAttrs = Object.assign({
                align: 'center'
            })
            return tableColAttrs
        },
        paginationAttrsInfo () {
            const paginationAttrs = Object.assign({
                pageLayout: 'total, sizes, prev, pager, next, jumper',
                pageSizes: [10, 20, 30, 40, 50]
            })
            return paginationAttrs
        }
    },
    watch: {
        paginationData: {
            handler (val) {
                this.paginationInfo = val
            },
            immediate: true
        }
    },
    methods: {
        handleSelectionChange (val) {
            this.$emit('update:multiSelection', val)
        },
        handleCurrentChange (val) {
            this.paginationData.pageNumber = val
            this.$emit('onCurrentChange', this.paginationData)
        },
        handleSizeChange (val) {
            this.$emit('onSizeChange', val)
        },
        indexMethod (index) {
            if (this.isPagination) {
                return this.paginationInfo.pageSize * (this.paginationInfo.pageNumber - 1) + index + 1
            } else {
                return index + 1
            }
        },
        formatter (data) {
            return (data || data === 0) || this.isNull ? data : '-'
        }
    }
}
</script>
<style lang="scss" scoped>
.el-pagination {
    margin: 20px 0;
    // float: right;
    text-align: right;
}
/deep/ .el-table th {
    font-size: 12px;
}
/deep/ .el-table td {
    font-size: 12px;
}
// 暂无数据
/deep/ .el-table__empty-block {
    min-height: 48px;
}
/deep/ .el-table__empty-text {
    line-height: 48px;
}
</style>
